<template>
  <div class="container">
    <img src="@img/bj.jpg" alt="" class="index-bj">
    <index-header></index-header>
    <alert-component :alertInfo="alertInfo" @hide="handleHideAlert"></alert-component>
  </div>
</template>

<script>
import IndexHeader from './components/Header'
import AlertComponent from '@comp/alert/Alert'
export default {
  name: 'Index',
  components: {
    IndexHeader,
    AlertComponent
  },
  data () {
    return {
      alertInfo: {
        title: '友情提示',
        content: '谢谢关注,本站正在努力建设中...敬请期待!!!',
        status: true
      }
    }
  },
  methods: {
    handleHideAlert (e) {
      this.alertInfo.status = e
    }
  }
}
</script>

<style scoped lang="stylus">
  .container
    display:flex
    flex-direction: column
    align-items:center
    .index-bj
      position:fixed
      left:0
      top:0
      right:0
      width:100%
      height:100%
</style>


