<template>
  <div class="header">
    <img src="@img/head.jpg" alt="" class="head-img">
    <p class="home-page-title">晓伟哥的个人主页</p>
    <p class="autograph">小小前端耕耘者</p>
  </div>
</template>

<script>
export default {
  name: 'IndexHeader'
}
</script>

<style lang="stylus" scoped>
  .header
    z-index:999
    text-align:center
    padding:40px 0
    .head-img
      width:130px
      height:130px
      border-radius:50%
      transition: all 1.6s
    .head-img:hover
      -webkit-transform: rotate(360deg);
      -moz-transform: rotate(360deg);
      -ms-transform: rotate(360deg);
      transform: rotate(360deg);
      -ms-transform: rotate(360deg); /* IE 9 */
      -webkit-transform: rotate(360deg); /* Safari and Chrome */
    .home-page-title
      padding:20px 0
      font-size:40px
      color:#fff
      transition: all 0.5s;
      font-weight: 400;
    .home-page-title:hover
      letter-spacing: 5px;
    .autograph
      font-size:26px
      color:#666
      letter-spacing: 5px;
</style>



